

import Vue from 'vue';
import App from './App';


import ObjectStroe from './store/index.js';
import ObjectRouter from './router/index.js';

import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import XHeader from 'vux/src/components/x-header/index.vue'


import { Http } from '@/class/http.js';
import Config from '../static/lib/appConfig.js';


Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);
Vue.use(ConfirmPlugin);

Vue.component(XHeader.name, XHeader);

Vue.config.productionTip = false;

Vue.prototype.$http = Http;
Vue.prototype.$config = Config;

/* eslint-disable no-new */
new Vue({
el: '#app',
store: ObjectStroe,
router: ObjectRouter,
components: { App },
template: '<App/>',
})



