

import InlineXSwitch from 'vux/src/components/inline-x-switch/index.vue'

import { Common } from '@/class/common.js';
import JSEncrypt from '../../static/lib/encrypt.min.js';

export default {
name: 'TagSignIn',
components: { InlineXSwitch },
props: ['ParentCpnt'],
data(){
return{
txtTel: '',
txtPwd: '',
pwdSwitch: false,
}
},

methods: {
telInputHandler: function (event) {
this.txtTel = event.target.value.replace(/[^0-9]+/g, '');
},

checkForm: function() {
if(Common.verify.isEmpty(this.txtTel)){
this.$vux.toast.text('手机号码不能为空');
return false;
}
if(!Common.verify.mobile(this.txtTel)){
this.$vux.toast.text('手机号码格式有误');
return false;
}
if(!Common.verify.password(this.txtPwd)){
this.$vux.toast.text('密码格式有误');
return false;
}
return true;
},

registerLinkClick:function(){
this.$router.push({ name:'SignUp' });
},

loginClick: function() {
if(!this.checkForm()) return;

try{
this.$vux.loading.show();
(async ()=>{
let publickey = await this.getPublicKey();
this.doSignIn(publickey);
this.$vux.loading.hide();
})();
}catch(e){
this.$vux.loading.hide();
this.$vux.toast.show({text:e, type:'warn'});
}
},

getPublicKey: function(){
return new Promise((resolve, reject)=>{
this.$http.request({
url: this.$config.domain +'/static/data/getPublicKey.json',
success: (dic) => {
if(dic.code==0){
resolve(dic.result);
}else{
reject(dic.msg);
}
},
error:(err) => {
reject(err);
}
});
});
},

doSignIn: function(publicKey){
return new Promise((resolve,reject)=>{
let encrypt = new JSEncrypt();
encrypt.setPublicKey(publicKey);
let ecPwd = encrypt.encrypt(this.txtPwd);

let data = {
"userName": this.txtTel,
"password" : ecPwd,
"appType" : this.$config.appType,
"deviceName": this.$config.channel,
"deviceModel": this.$config.channel,
"deviceId": this.$config.channel,
"osName": this.$config.channel,
"osVersion": this.$config.channel,
"appVersion": this.$config.channel,
};

this.$http.request({
url: this.$config.domain +'/static/data/pandaLogin.json',


data: data,
success: (dic) => {
if(dic.code==0){
resolve(true);
this.$store.commit("setLoginState",dic.result);
if(this.ParentCpnt=='TabHome'){

}
else{
this.$router.back(-1);
}
}else{
reject(dic.msg);
}
},
error: (err) =>{
reject(err);
}
});
});
}
}
}





