

import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default{
name: 'TagTabBar',
components: { Tabbar, TabbarItem },


computed:{
selectedIndex: function(){
return this.$store.state.tabIndex;
}
},

watch: {
selectedIndex: function(index, prev){
switch(index){
case 1:
this.$router.push({ name: 'TabHome' });
break;
case 2:
this.$router.push({ name: 'TabMine' });
break;
}
}
},

methods: {
tabOnChange: function (i) {
this.$store.commit('setTabIndex', i+1);
},

















}

}




